import React, { useState, useEffect } from "react";
import { Table } from "antd";
import "antd/dist/antd.css";

const DynamicChildColumn = () => {
  const [laneData, setLaneData] = useState([
    {
      lane_num_statistics: [
        {
          lane_num: "1",
          data: [
            {
              unit_time: "5",
              volume: "1",
            },
            {
              unit_time: "10",
              volume: "11",
            },
            {
              unit_time: "30",
              volume: "111",
            },
            {
              unit_time: "1",
              volume: "1111",
            },
            {
              unit_time: "1",
              volume: "11111",
            },
          ],
        },
        {
          lane_num: "2",
          data: [
            {
              unit_time: "5",
              volume: "2",
            },
            {
              unit_time: "10",
              volume: "22",
            },
            {
              unit_time: "30",
              volume: "222",
            },
            {
              unit_time: "1",
              volume: "2222",
            },
            {
              unit_time: "1",
              volume: "22222",
            },
          ],
        },
      ],
    },
  ]);
  const newArr = [];

  laneData[0].lane_num_statistics.forEach((item, idx) => {
    const lanemNum = item.lane_num;
    item.data.map((elem) => {
      if (idx === 0) {
        newArr.push({
          time: elem.unit_time,
          [lanemNum + "_volume"]: elem.volume,
        });
      } else {
        for (let i = 0; i < newArr.length; i++) {
          console.log("newArdffr", newArr);
          console.log("elem", elem);
          if (newArr[i].time === elem.unit_time) {
            newArr.splice(i, 1, {
              ...newArr[i],
              [lanemNum + "_volume"]: elem.volume,
              //newArr      +   
            });
            break;
          }
        }
      }

      return newArr;
    });
  });
  const columns = [
    {
      title: "",
      dataIndex: "time",
      key: "time",
      align: "center",
      width: "20%",
    },
    {
      title: " ()",
      dataIndex: "car_volume",
      key: "car_volume",
      align: "center",
      children: laneData[0].lane_num_statistics.map((item) => {
        return {
          title: `${item.lane_num}`,
          dataIndex: `${item.lane_num}_volume`,
          key: `${item.lane_num}_volume`,
          align: "center",
        };
      }),
    },
  ];

  return (
    <>
      <Table
        columns={columns}
        style={{ width: "80%", margin: "0 auto" }}
        dataSource={newArr}
        bordered
      />
    </>
  );
};

export default DynamicChildColumn;
