import React, { useState, useRef, useEffect } from "react";
import { Select, Divider, Space, Input, message } from "antd";
import "antd/dist/antd.css";

const SelectType = () => {
  const inputRef = useRef(null);
  const [items, setItems] = useState(["", "", "", ""]);
  const [name, setName] = useState("");
  const [selectedType, setSelectedType] = useState("");
  const [koreanType, setKoreanType] = useState("");

  useEffect(() => {
    console.log("selectedType", selectedType);
  }, [selectedType, koreanType]);

  const isEndWithConsonant = (korean) => {
    const finalChrCode = korean.charCodeAt(korean.length - 1);
    const consonantCode = (finalChrCode - 44032) % 28;
    if (consonantCode === 0) {
      //0   -> 
      setKoreanType(`${korean}`);
    } else {
      setKoreanType(`${korean}`);
    }
  };
  const onNameChange = (e) => {
    setName(e.target.value);
  };
  const onEnter = (e) => {
    e.preventDefault();
    if (e.target.value === undefined || e.target.value === "") {
      message.warning(" ");
      setItems([...items]);
    } else {
      setItems([...items, name]);
      setName("");
      setTimeout(() => {
        inputRef.current.focus();
      }, 0);
    }
  };
  const onSelect = (e) => {
    setSelectedType(e);
    isEndWithConsonant(e);
  };

  return (
    <>
      <div style={{ paddingTop: 40, paddingBottom: 100 }}>
         
        <div>
          <Select
            onChange={onSelect}
            style={{ width: 350 }}
            placeholder=" "
            className="custom_select"
            autoFocus
            dropdownRender={(menu) => (
              <>
                {menu}
                <Space style={{ padding: "10px 8px 4px" }}>
                  <Input
                    placeholder="Enter  "
                    ref={inputRef}
                    style={{ width: 250 }}
                    value={name}
                    onChange={onNameChange}
                    onPressEnter={onEnter}
                  ></Input>
                </Space>
              </>
            )}
            options={
              items && items.map((item) => ({ label: item, value: item }))
            }
          ></Select>
        </div>
        {koreanType === "" ? (
          <div>   ?</div>
        ) : (
          <div>  {koreanType} !</div>
        )}
      </div>
    </>
  );
};
export default SelectType;
